const getLang = () => {
  const lang = localStorage.getItem('lang')
  return (lang === 'en' ?
  (category) => (term) => english[category][term]
  : (category) => (term) => french[category][term])
}

const french = {
  navbar: {
    about: 'A propos',
    projects: 'Projets',
    contact: 'Contact',
    lang: 'EN'
  },
  header: {
    title: 'Analyste, Développeur, Designer.'
  },
  about: {
    title: 'A propos',
    first: "De la compréhension des besoins jusqu'à la mise en place de la solution informatique, je suis en mesure de réaliser toutes les tâches nécessaires à l'accomplissement de mes missions.",
    second: "Pour cela, je suis sans cesse en apprentissage de nouvelles méthodologies et technologies afin de connaître et choisir la meilleure solution."
  },
  projects: {
    title: 'Projets',
    description: 'Description',
    realize: 'Réalisé avec'
  }
}

const english = {
  navbar: {
    about: 'About',
    projects: 'Projects',
    contact: 'Contact',
    lang: 'FR'
  },
  header: {
    title: "Analyst, Developer, Designer."
  },
  about: {
    title: 'About',
    first: "From the understanding of the needs to the implementation of the IT solution, I'm able to carry out all the tasks necessary for the accomplishment of my missions.",
    second: "To realize this, I'm constantly learning new methodologies and technologies in order to know and choose the best solution."
  },
  projects: {
    title: 'Projects',
    description: 'Description',
    realize: 'Made with'
  }
}

export default getLang



